


















































import Vue from 'vue'
import { saveOrUpdate, getEditMenuInfo, getMenuById } from '@/services/menu.ts'

export default Vue.extend({
    name: 'menuCreateOrEdit',
    props: {
        isEdit: {
            type: Boolean,
            default: false,
        },
    },
    data () {
        return {
            loading: false,
            form: {
                parentId: -1, // -1 表示没有上级菜单
                name: '123',
                href: '123',
                icon: '123',
                orderNum: 0,
                description: '123',
                shown: false,
            },
            parentMenuList: [], // 父级菜单列表
        }
    },
    created () {
        this.getMenuInfo()
        this.loadMenuById()
    },
    methods: {
        async getMenuInfo () {
            const { data } = await getEditMenuInfo()
            if (data.code === '000000') {
                this.parentMenuList = data.data.parentMenuList
            }
        },
        async loadMenuById () {
            if (this.$route.params.id) {
                const { data } = await getMenuById(this.$route.params.id)
                this.form = data.data
            }
        },
        async onSubmit () {
            try {
                this.loading = true
                const { data } = await saveOrUpdate(this.form)
                this.loading = false
                if (data.code === '000000') {
                    this.$message.success('提交成功')
                    this.$router.back()
                } else {
                    this.$message.error(`提交失败:${data.mesg}`)
                }
            } catch (error) {
                this.loading = false
                this.$message.error(`提交失败${error.message}`)
            }
        },
    },
})
